.loader {
  width: 100%;
  height: 4.8px;
  background: rgba(255, 255, 255, .15);
  display: inline-block;
  position: relative;
  overflow: hidden;
}

.loader:after {
  content: "";
  width: 192px;
  height: 4.8px;
  box-sizing: border-box;
  background: #3abbfc;
  animation: animloader 2s linear infinite;
  position: absolute;
  top: 0;
  left: 0;
}

@keyframes animloader {
  0% {
    left: 0;
    transform: translateX(-100%);
  }

  100% {
    left: 100%;
    transform: translateX(0%);
  }
}

